body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App-header {
  background-color: black;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 3vmin);
  color: white;
  padding: 5vh 5vw 5vh 5vw
}

a:link {
  color: white;
}

a:visited {
  color: white;
}

a:hover {
  color: white;
}

a:active {
  color: white;
}

* { box-sizing: border-box; }

.carousel {
  background: #FAFAFA;
  height: 100%;
}

.carousel-cell {
  width: 100%;
  height: 80vh;
  margin-right: 10px;
  margin-left: 10px;
  background: black;
  border-radius: 25px;
  text-align: left;
  padding: 5vh 5vw 5vh 5vw;
  font-size: 12;
  word-wrap: break-word;
  display: flex;
  flex-direction: column;
}

.flickity-prev-next-button {
  background: transparent;
  visibility: hidden;
}

.flickity-button {
  visibility: hidden;
}

.flickity-page-dots {
  bottom: -22px;
}

.flickity-page-dots .dot {
  height: 4px;
  width: 40px;
  margin: 0;
  border-radius: 0;
}

.overlap-title {
  position: relative;
  color: white;
  font-size: 1.5em;
}

.overlap-text {
  position: relative;
  color: white;
  margin-top: auto;
}

.overlap-shape {
  position: relative;
  background-color: white;
  height: 1px;
  width: 100%;
}

